import ApiCall from "./apicall"

export default async function CategoryCard(props) {
    const products = await ApiCall()

    return (
        <div className="w-[98vw] h-[35vh] m-auto mt-[5vh] bg-white overflow-hidden grid gap-1 items-center grid-cols-1 lg:grid-cols-4 xl:grid-cols-6">
            <div className="col-span-6 text-xl ml-2 mb-4 mt-2 capitalize"><b>{props.category}</b></div>
            {products
            .filter((product) => product.category === props.category)
            .map((product) => {
                return (
                    <div key={product.id} className=" w-[100%] h-[28vh]">
                        <img className="w-full h-[60%] object-contain mb-5" src={product.image} alt="" />
                        <p className="w-full h-[10%] text-center line-clamp-1">{product.title}</p>
                        <p className="text-center">RS.{product.price}</p>
                    </div>
                )
            })}

        </div>
    )
}


